<template>
    <panel :config="config" class="buddies-activity">
        <template slot="header"><i class="fanet anet-buddies">&nbsp;</i>{{ title }}</template>
        <template slot="table_responsive">
            <template v-if="activities === null"><LoadingComp></LoadingComp></template>
            <template v-else>
                <table class="table buddies activity">
                    <keep-alive>
                    <tbody v-if="activities.buddies.length > 0">
                        <tr v-for="buddy in activities.buddies" v-if="buddy.user_activity && games[buddy.user_activity.game_id]" :key="buddy.id" class="clickable">
                            <template v-if="['sm', 'xs', 'xxs'].includes($mq)">
                            <td class="buddy_game"><a :href="profileUrl(buddy.id)" :target="isIn3rdParty ? '_blank' : '_self'"><img class="icon avatar fixed" :src="buddy.avatar" @error="avatarError"/></a><br/><a :href="gameUrl(buddy.user_activity.game_id)" @click.prevent.stop="goToGame(buddy.user_activity.game_id)"><img v-if="games[buddy.user_activity.game_id].icon" :src="games[buddy.user_activity.game_id].icon" class="icon game fixed" @error="gameIconError"/></a></td>
                            </template>
                            <template v-else>
                            <td class="buddy icon"><a :href="profileUrl(buddy.id)" :target="isIn3rdParty ? '_blank' : '_self'"><img :src="buddy.avatar" class="icon avatar fixed" @error="avatarError"/></a></td>
                            <td class="game icon"><a :href="gameUrl(buddy.user_activity.game_id)" @click.prevent.stop="goToGame(buddy.user_activity.game_id)"><img v-if="games[buddy.user_activity.game_id].icon" :src="games[buddy.user_activity.game_id].icon" class="icon game fixed" @error="gameIconError"/></a></td>
                            </template>
                            <td>
                                <a :href="gameUrl(buddy.user_activity.game_id)" @click.prevent.stop="goToGame(buddy.user_activity.game_id)">
                                    <div class="chevron-action"><font-awesome-icon icon="chevron-right" size="2x" /></div>
                                    <ul>
                                        <li class="buddy name">{{ buddy.login_name }}</li>
                                        <li class="game name">{{ games[buddy.user_activity.game_id].name }}</li>
                                        <li><template v-if="['xs', 'xxs'].includes($mq)"><time :date="buddy.user_activity.last_used">{{ $moment.unix(buddy.user_activity.last_used).format('L HH:mm') }}</time></template><template v-else>{{ getTrans('last_used') }} <time :date="buddy.user_activity.last_used">{{ $moment.unix(buddy.user_activity.last_used).format('LLLL') }}</time></template></li>
                                        <li v-if="isRankable(buddy.user_activity.game_id)"><a :href="urlFor('history-game', {'id': buddy.user_activity.game_id}) + '?u=' + buddy.id" @click.prevent.stop="goHistory" class="history-game">{{ getTrans('game_history_link') }}</a></li>
                                    </ul>
                                </a>
                            </td>
                        </tr>
                        <tr v-if="currentPage > 1" v-for="i in Math.max((itemsPerPage - activities.buddies.length) || 0)" :key="i">
                            <td class="buddy icon"></td>
                            <td class="game icon"></td>
                            <td></td>
                        </tr>
                    </tbody>
                    <tbody v-else>
                        <tr><td>{{ getTrans('nobody') }}</td></tr>
                    </tbody>
                    </keep-alive>
                    <tfoot v-if="pageCount > 1">
                        <tr>
                            <td colspan="3" style="text-align:center;">
                                <pagination @navigate="getActivities" :current-page="currentPage" :page-count="pageCount" :lang="lang" size="small"></pagination>
                            </td>
                        </tr>
                    </tfoot>
                </table>
            </template>
        </template>
    </panel>
</template>

<script>
import axios from 'axios';
import GenericMixin from '@mixins/generic';
import HTTP from '@/httpapi';
import Games from '@api/games';
import Pagination from '@/components/Pagination';
import Paginate from '@lib/paginate';
import { cacheBasic } from '@/lib/cache';

// TODO check webpack handles common chunks correctly so that we can safely
// remove the following imports
// import bus from '@/event_bus'
// import LoadingComp from '@/components/LoadingComp'

export default {
    name: 'buddies_activities',

    mixins: [GenericMixin],

    components: {
        'pagination': Pagination,
    },

    computed: {
        lastUsedTrans () {
            return this.config.last_used;
        },
        pageCount () {
            if (!this.activities) {
                return 0;
            }

            return Math.ceil(this.activities.total / this.itemsPerPage);
        }
    },

    methods: {
        getActivities (page) {
            let t = this;

            axios.all([
                Paginate('/v1/user/me/buddies', page, {sort: 'activity', extras: 'last_activity,avatar', limit: this.itemsPerPage}, {total: (this.activities ? this.activities.total : 0), cache: cacheBasic}),
                Games.all(),
                Games.rankables()
            ]).then(axios.spread(function (acts, games, rankables) {
                t.rankables = rankables.data.rankingGames;
                t.games = games.data.games;
                t.activities = acts.data;
                if (page) {
                    t.currentPage = page;
                }
                if (t.pageCount > 1 && t.currentPage === 1) {
                    setTimeout(function () {
                        var el = document.querySelector('.buddies-info .panel .table-responsive table');
                        el.style.minHeight = el.offsetHeight + 'px';
                        document.querySelector('.buddies-info .panel .table-responsive table tr td').style.maxHeight = '85px';
                        document.querySelector('.buddies-info .panel .table-responsive table tr td').style.height = '85px';
                    }, 5);
                }
            }));
        },

        profileUrl (userId) {
            return this.baseHost + '/' + this.lang + '/profile/' + userId;
        },

        isRankable (gameId) {
            return (this.rankables && this.rankables.findIndex(function (gid) { return gid === gameId; }) >= 0);
        },

        goHistory (ev) {
            if (this.isIn3rdParty) {
                window.open(ev.target.href);
            } else {
                window.location = ev.target.href;
            }
        }
    },

    mounted () {
        HTTP.ifInitialized(this.getActivities);
    },

    data () {
        return {
            itemsPerPage: 10,
            currentPage: 1,
            activities: null,
            games: null,
            rankables: null
        };
    }
};

</script>

<style lang="scss" scoped>
@import '~sass/_variables.scss';
@import '~sass/image.scss';
@import '~sass/table.scss';

.buddies-activity /deep/ {
    .panel-heading {
         h1 {
            i {
                font-size: 12px;
                margin-left: -14px;
            }
        }
    }

    .table {
        tbody {
            vertical-align: top;

            .buddy_game {
                img:nth-child(1) {
                    border-radius: 50%;
                    margin-bottom: 2px;
                }
            }

            a {
                color: #333;
                text-decoration: none;

                .chevron-action {
                    bottom: -5px;
                }
            }

            tr {
                &.clickable:hover {
                    .buddy.icon {
                        background-color: $panel-background-color;
                    }
                    .fa-chevron-right {
                        color: white;
                    }

                }

                td {
                    border: none;
                    vertical-align: middle;

                    &.buddy {
                        img {
                            border-radius: 50%;
                        }
                    }

                    .history-game {
                        color: map-get($asnet-colors, light-purple);
                        display: inline-block;
                        padding: 5px;
                        text-decoration: none;
                        z-index: 100;

                        &:hover {
                            background-color: map-get($asnet-colors, light-purple);
                            color: white;
                        }
                    }
                }
            }

            ul {
                list-style: none;
                padding: 0;
                text-decoration: none;

                li {
                    font-size: 0.9em;

                    &.buddy {
                        color: #831fdb;
                    }

                    &.name {
                        font-weight: 500;
                        font-size: 1.1em;
                    }
                }
            }

            .chevron-action {
                float: right;
                height:50px;
                position: relative;
                text-align:center;
                bottom: 20px;
                vertical-align: middle;

                .fa-chevron-right {
                    position: relative;
                    top: 15px;
                    color: lightgray;
                    font-weight: bold;
                }
            }

            :hover > .chevron-action .fa-chevron-right {
                color: white;
            }

            .buddy.icon:hover ~ td {
                background-color: $panel-background-color;
            }
            .buddy.icon:hover ~ td .fa-chevron-right {
                color: lightgray;
            }
        }
    }
}
</style>



